<template>
  <div calss="summary">
      <transition name="fade">
    <img v-if="show" class="logo" src="../../../static/logo.png"   style="height:328px;width:951px"  alt="logo"  @click="gotomain"/>
  </transition>	
  </div>
</template>

<script>
export default {
  data () {
    return {
      show:false
    }
  },
  methods:{
  gotomain(){
    this.$router.push({path:"/main"});
  },
  changeshow(){
    this.show=true
  }
    },
  mounted:function(){
      this.$nextTick(function () {
                setInterval(this.changeshow, 50);
            })
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
img{
 cursor:pointer;
 width: 100%;
 height: auto;max-width: 100%; display: block;
}
.summary{
  position: relative;
}
.logo{
   top: 10px;
   left:10px;
   right: 0;
   bottom: 0;
   margin: auto;
   position:absolute;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity 5s;
}
</style>




